import React from 'react';
import symptom from '../images/symptoms/symptoms-1.png';
import Fade from 'react-reveal/Fade';

export default function Symptoms() {
    return (
        <>
            <section id='symptoms' className='position-relative mySec overflow-hidden'>
                <div className='container'>
                    <div className='row g-4 align-items-center'>
                        <Fade left>
                            <div className='col-md-6'>
                                <div className='symptoms-left'>
                                    <h6>Main Symptoms</h6>
                                    <h2>What Are The Main Symptoms?</h2>
                                    <p className='myP'>The COVID-19 virus affects different people in different ways.COVID-19 is a respiratory disease and most infected people will develop mild to moderate symptoms and recover without requiring special treatment. People who have underlying medical conditions and those over 60 years old have a higher risk of developing severe disease and death.</p>
                                    <div className='row g-2'>
                                        <div className='col-lg-6 col-md-12 col-sm-6'>
                                            <div>
                                                <p className='fw-bold mb-1'>Common Symptoms Include:</p>
                                                <ul className='symp'>
                                                    <li>Fever</li>
                                                    <li>Tiredness</li>
                                                    <li>Dry Cough</li>
                                                </ul>
                                            </div>
                                        </div>
                                        <div className='col-lg-6 col-md-12 col-sm-6'>
                                            <div>
                                                <p className='fw-bold mb-1'>Other Symptoms Include:</p>
                                                <ul className='symp'>
                                                    <li>Shortness of Breath</li>
                                                    <li>Aches and Pains</li>
                                                    <li>Sore Throat</li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Fade>
                        <Fade right>
                            <div className='col-md-6'>
                                <div className='img'>
                                    <img src={symptom} className='w-100' alt="" />
                                </div>
                            </div>
                        </Fade>
                    </div>
                </div>
            </section>
        </>
    )
}
