import React from 'react';
import { Link } from 'react-router-dom';
import new1 from '../images/newFeed/news-1.png';
import new2 from '../images/newFeed/news-2.png';
import new3 from '../images/newFeed/news-3.png';

import author1 from '../images/newFeed/author-1.png';
import author2 from '../images/newFeed/author-2.png';
import author3 from '../images/newFeed/author-3.png';

import virus1 from '../images/carousel/anim-icon-1.png';
import virus2 from '../images/carousel/anim-icon-2.png';
import virus3 from '../images/carousel/anim-icon-3.png';
import Fade from 'react-reveal/Fade';

export default function NewFeeds() {

    let authors = [,
        { id: 1, mainImg: new1, name: 'Lynda Stone', desc: 'Economic Policies for The COVID-19 War', date: 'April 20, 2020', authorImg: author1 },
        { id: 2, mainImg: new3, name: 'Sara Casey', desc: 'Maintaining Banking System Safly', date: 'April 18, 2020', authorImg: author3 },
        { id: 3, mainImg: new2, name: 'David Marks', desc: 'Some Countries Provide Clear and Helpful...', date: 'April 19, 2020', authorImg: author2 },

    ];

    return (
        <>
            <section id='new-feeds' className='position-relative overflow-hidden'>
                <div className='container'>

                    <Fade top>
                        <div className='title'>
                            <h6>Our Article</h6>
                            <h2>Stay Updated with Our <br />
                                News Feed.</h2>
                        </div>
                    </Fade>


                    <div className='row g-4'>
                        {authors.map((author) => (

                            <Fade bottom key={author.id}>
                                <div className='col-lg-4 col-md-6'>
                                    <div className='item'>
                                        <div className='img'>
                                            <div>
                                                <img src={author.mainImg} className='w-100 main' alt="" />
                                            </div>
                                            <div className='layer'></div>
                                        </div>
                                        <div className='info'>
                                            <h6>FEATURED</h6>
                                            <Link to=''>{author.desc}</Link>
                                            <ul>
                                                <li><img src={author.authorImg} className='me-2' alt="" />Lynda Stone</li>
                                                <li className='text-muted'>{author.date}</li>
                                            </ul>
                                            <p>Lorem ipsum dolor sit amet consectetur adipisicing sed.</p>
                                            <Link className='my-btn' to=''>See Details</Link>
                                        </div>
                                    </div>
                                </div>
                            </Fade>


                        ))}
                    </div>
                    <img src={virus1} className='virus1' alt="" />
                    <img src={virus2} className='virus2' alt="" />
                    <img src={virus3} className='virus3' alt="" />
                </div>
            </section>
        </>
    )
}
