import React from 'react';
import wash1 from '../images/sanity/hadnwash-1.png';
import wash2 from '../images/sanity/hadnwash-2.png';
import wash3 from '../images/sanity/hadnwash-3.png';
import wash4 from '../images/sanity/hadnwash-4.png';
import wash5 from '../images/sanity/hadnwash-5.png';
import wash6 from '../images/sanity/hadnwash-6.png';
import virus1 from '../images/sanity/anim-icon-1.png';
import virus2 from '../images/sanity/anim-icon-2.png';
import virus3 from '../images/sanity/anim-icon-3.png';
import virus4 from '../images/sanity/anim-icon-5.png';
import virus5 from '../images/sanity/anim-icon-6.png';
import Fade from 'react-reveal/Fade';

export default function Sanity() {

    let washSteps = [
        { id: 1, img: wash1, text: 'Apply Soap on Hand' },
        { id: 2, img: wash2, text: 'Palm to Palm' },
        { id: 3, img: wash3, text: 'Between Fingers' },
        { id: 4, img: wash4, text: 'Back of The Hands' },
        { id: 5, img: wash5, text: 'Clean with Water' },
        { id: 6, img: wash6, text: 'Use Towel to Dry' },
    ];

    return (
        <>
            <section id='sanity' className='position-relative overflow-hidden'>
                <div className='container'>
                    <Fade top>
                        <div className="title">
                            <h6>Wash Your Hands</h6>
                            <h2>How to Wash Your Hand <br />
                                Properly</h2>
                        </div>
                    </Fade>

                    <div className='row g-4'>
                        {washSteps.map((step) => (

                            <Fade bottom key={step.id}>
                                <div className='col-sm-6 col-md-4 col-lg-2'>
                                    <div className='item'>
                                        <div className='position-relative'>
                                            <img src={step.img} className='w-100 rounded-circle' alt="" />
                                            <span>{step.id}</span>
                                        </div>
                                        <h6 className='text-white fw-bold mt-3 mb-2'>{step.text}</h6>
                                    </div>
                                </div>
                            </Fade>
                        ))}
                    </div>
                    <img src={virus1} className='virus1' alt="" />
                    <img src={virus2} className='virus2' alt="" />
                    <img src={virus3} className='virus3' alt="" />
                    <img src={virus4} className='virus4' alt="" />
                    <img src={virus5} className='virus5' alt="" />
                    <img src={virus1} className='virus6' alt="" />
                </div>
            </section>
        </>
    )
}
