import React, { useEffect, useState } from 'react';
import about1 from '../images/about/about-1.png';
import virus from '../images/about/anim-icon-4.png';
import Fade from 'react-reveal/Fade';


export default function About() {


    useEffect(() => {
        virusMenu();
    }, []);

    const [list, setList] = useState(true);

    function virusMenu() {

        let lis = document.querySelectorAll('.tab-btn-box ul li');
        lis.forEach((li) => {
            li.addEventListener('click', () => {
                lis.forEach(l => l.classList.remove('active'));
                li.classList.add('active');
                if (li.classList.contains('one')) {
                    setList(true);
                } else {
                    setList(false);
                }
            })
        })
    };


    return (
        <>
            <section id='about' className='py-5 position-relative mySec overflow-hidden'>
                <div className='container'>
                    <div className='about-title mb-5'>
                        <h6>About Corona</h6>
                        <h2>Coronavirus Disease (COVID-19) <br />
                            Outbreak Situation</h2>
                    </div>
                    <div className='row g-4'>

                        <Fade left>
                            <div className='col-md-6'>
                                <div className='img'>
                                    <img src={about1} className='w-100' alt="" />
                                </div>
                            </div>
                        </Fade>
                        <Fade right>
                            <div className='col-md-6'>
                                <div>
                                    <div className='tabs-box'>
                                        <div className='tab-btn-box'>
                                            <ul>
                                                <li className='one active'>Overview</li>
                                                <li className='two'>Treatment</li>
                                            </ul>
                                        </div>
                                        <div className='tabs-content'>
                                            <div className='overview' style={list ? { display: 'block' } : { display: 'none' }}>
                                                <p>Coronavirus disease (COVID-19) is an infectious disease caused by a newly discovered coronavirus.</p>
                                                <p>Most people infected with the COVID-19 virus will experience mild to moderate respiratory illness and recover without requiring special treatment. Older people, and those with underlying medical problems like cardiovascular disease, diabetes, chronic respiratory disease, and cancer are more likely to develop serious illness.</p>
                                            </div>
                                            <ul className='treatment' style={list ? { display: 'none' } : { display: 'block' }}>
                                                <li>Avoid touching your face.</li>
                                                <li>Cover your mouth and nose when coughing.</li>
                                                <li>Stay home if you feel unwell.</li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Fade>
                    </div>
                </div>
                <img src={virus} className='virus' alt="" />
            </section>
        </>
    )
}
