import React from 'react';
import { Link } from 'react-router-dom';
import Fade from 'react-reveal/Fade';

export default function BookDoctor() {
    return (
        <>
            <section id='book-doctor' className='position-relative overflow-hidden'>
                <div className='container'>
                    <div className='row align-items-center g-4'>

                        <Fade top>
                            <div className='col-xl-6'>
                                <div className='item'>
                                    <h3>Want To Book A Doctor or Test COVID-19?</h3>
                                </div>
                            </div>
                        </Fade>
                        <Fade bottom>
                            <div className='col-xl-6'>
                                <div className='btns d-flex flex-column flex-sm-row align-items-center justify-content-center'>
                                    <div className='d-flex align-items-center justify-content-center m-1'>
                                        <div className='btn-outer mb-2'><Link to=''>Book A Doctor</Link></div>
                                    </div>
                                    <div className='m-1'>
                                        <div className='outer-btn'><Link to=''>Test Covid-19</Link></div>
                                    </div>
                                </div>
                            </div>
                        </Fade>

                    </div>
                </div>
            </section>
        </>
    )
}
