import React from 'react';
import { Link } from 'react-router-dom';
import logo from '../images/logo.png';
import Accordion from 'react-bootstrap/Accordion';
import { useState, useEffect } from 'react';

export default function Navbar() {


    // set active nav tab with scroll
    const [tabs, setTabs] = useState([]);
    const [scroll, setScroll] = useState(false);

    useEffect(() => {
        let x = Array.from(document.querySelectorAll('.myNav'));
        setTabs(x);
    }, [])
    // active nav tab
    if (tabs.length > 0) {
        tabs.forEach((tab) => {
            tab.addEventListener('click', () => {
                tabs.forEach((one) => {
                    one.classList.remove('active')
                })
                tab.classList.add('active');
            })
        })
    }
    // show & hide scroll to top
    window.addEventListener('scroll', () => {
        if (window.scrollY > 200) {
            setScroll(true);
        } else {
            setScroll(false);
        }
    })


    // scroll to top
    function scrollTop() {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    }

    return (
        <>
            <nav className='navbar navbar-expand-lg navbar-light bg-white sticky-top'>
                <div className="container">
                    <div className='w-100'>
                        <div className='row align-items-center g-4'>
                            <div className='col-xl-2 text-center'>
                                <Link className="navbar-brand" to=''>
                                    <img src={logo} alt="" />
                                </Link>
                            </div>
                            <div className='col-xl-10'>
                                <div className='d-flex align-items-center justify-content-xl-end justify-content-between flex-row-reverse flex-lg-row'>
                                    <div>
                                        <button className="navbar-toggler" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasDarkNavbar" aria-controls="offcanvasDarkNavbar">
                                            <span className="navbar-toggler-icon"></span>
                                        </button>
                                        <div data-bs-scroll="true" className="offcanvas offcanvas-end" tabIndex="-1" id="offcanvasDarkNavbar" aria-labelledby="offcanvasDarkNavbarLabel">
                                            <div className="offcanvas-header">
                                                <button type="button" className="ms-auto offcanvasBtn" data-bs-dismiss="offcanvas" aria-label="Close"><i className="fa-solid fa-xmark text-white"></i></button>
                                            </div>
                                            <div className="offcanvas-body p-0">
                                                <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                                                    <li className="nav-item">
                                                        <a className='nav-link myNav active' aria-current="page" href="#my-carousel">Home</a>
                                                    </li>
                                                    <li className="nav-item">
                                                        <a className='nav-link myNav' href="#about">About</a>
                                                    </li>
                                                    <li className="nav-item">
                                                        <a className='nav-link myNav' href="#symptoms">Symptoms</a>
                                                    </li>
                                                    <li className="nav-item">
                                                        <a className='nav-link myNav' href="#protect">Prevention</a>
                                                    </li>
                                                    <li className="nav-item">
                                                        <a className='nav-link myNav' href="#question">FAQ</a>
                                                    </li>

                                                    <li className="nav-item d-lg-none d-block">
                                                        <Accordion>
                                                            <Accordion.Item eventKey="0">
                                                                <Accordion.Header>News</Accordion.Header>
                                                                <Accordion.Body>
                                                                    <ul className='list-unstyled'>
                                                                        <li className='nav-link'><a href="#" className='ms-4'>Blog Grid</a></li>
                                                                        <li className='nav-link'><a href="#" className='ms-4'>Blog Sidebar</a> </li>
                                                                        <li className='nav-link'><a href="#" className='ms-4'>Blog Details</a> </li>
                                                                    </ul>
                                                                </Accordion.Body>
                                                            </Accordion.Item>
                                                        </Accordion>
                                                    </li>
                                                </ul>
                                                <ul className='list-unstyled text-white px-2 my-5 d-lg-none d-block'>
                                                    <li><h5 className='fw-bold'>Contact Info</h5></li>
                                                    <li className='contact'>Chicago 12, Melborne City, USA</li>
                                                    <li className='contact'>+88 01682648101</li>
                                                    <li className='contact'>info@example.com</li>
                                                </ul>
                                                <ul className='social list-unstyled d-flex d-lg-none px-4 justify-content-between'>
                                                    <li><a href="#" className='text-decoration-none text-white'><i className="fa-brands fa-twitter"></i></a></li>
                                                    <li><a href="#" className='text-decoration-none text-white'><i className="fa-brands fa-facebook-f"></i></a></li>
                                                    <li><a href="#" className='text-decoration-none text-white'><i className="fa-brands fa-pinterest-p"></i></a></li>
                                                    <li><a href="#" className='text-decoration-none text-white'><i className="fa-brands fa-instagram"></i></a></li>
                                                    <li><a href="#" className='text-decoration-none text-white'><i className="fa-brands fa-youtube"></i></a></li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                    <div className='outer-btn'><Link to=''><i className="fa-solid fa-phone-flip me-2"></i>Emergency Call</Link></div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

                <div id='scrollToTop' onClick={scrollTop} style={scroll ? { opacity: '1' } : { opacity: '0' }}>
                    <i className="fa-solid fa-circle-arrow-up fa-2x"></i>
                </div>

            </nav>
        </>
    )
}
