import React from 'react';
import { Link } from 'react-router-dom';
import footerLogo from '../images/footer/footer-logo.png';

import virus1 from '../images/carousel/anim-icon-1.png';
import virus2 from '../images/carousel/anim-icon-2.png';
import Fade from 'react-reveal/Fade';

export default function Footer() {
    return (
        <>
            <footer className='position-relative overflow-hidden'>
                <div className='shape-layer'></div>
                <div className='container'>
                    <div className='row g-4 align-items-lg-start align-items-center'>

                        <Fade bottom>
                            <div className='col-lg-4 col-md-6'>
                                <div className='one'>
                                    <img src={footerLogo} className='w-50' alt="" />
                                    <p className='my-3'>
                                        The best way to prevent and slow down transmission is be well informed about the COVID-19 virus, the disease it causes and how it spreads.
                                    </p>
                                    <p className='m-0'>
                                        Protect yourself and others from infec- tion by washing your hands
                                    </p>
                                </div>
                            </div>
                        </Fade>
                        <Fade bottom>
                            <div className='col-lg-2 col-md-6'>
                                <div className='two'>
                                    <h3 className='mb-3'>Services</h3>
                                    <ul className='list-unstyled'>
                                        <li>About Us</li>
                                        <li>Symptoms</li>
                                        <li>Prevention</li>
                                        <li>Our Doctors</li>
                                        <li>Our Blog</li>
                                        <li className='mb-0'>Contact Us</li>
                                    </ul>
                                </div>
                            </div>
                        </Fade>
                        <Fade bottom>
                            <div className='col-lg-2 col-md-6'>
                                <div className='three'>
                                    <h3 className='mb-3'>Regions</h3>
                                    <ul className='list-unstyled'>
                                        <li>America</li>
                                        <li>Africa</li>
                                        <li>South East Asia</li>
                                        <li>Europe</li>
                                        <li>Australia</li>
                                        <li className='mb-0'>Westenr Pacific</li>
                                    </ul>
                                </div>
                            </div>
                        </Fade>
                        <Fade bottom>
                            <div className='col-lg-4 col-md-6'>
                                <div className='four'>
                                    <h3 className='mb-3'>Contacts</h3>
                                    <ul className='info list-unstyled'>
                                        <li><i className="fa-solid fa-location-dot me-3"></i>Flat 20, Reynolds Neck, North Helenaville, FV77 8WS</li>
                                        <li><i className="fa-solid fa-phone me-3"></i>+2(305) 587-3407</li>
                                        <li><i className="fa-solid fa-envelope me-4"></i>info@example.com</li>
                                    </ul>
                                    <ul className='social'>
                                        <li><Link to=''><i className="fa-brands fa-facebook-f"></i></Link></li>
                                        <li><Link to=''><i className="fa-brands fa-twitter"></i></Link></li>
                                        <li><Link to=''><i className="fa-brands fa-vimeo-v"></i></Link></li>
                                        <li><Link to=''><i className="fa-brands fa-linkedin-in"></i></Link></li>
                                    </ul>
                                </div>
                            </div>
                        </Fade>
                    </div>
                </div>
                <img src={virus1} className='virus1' alt="" />
                <img src={virus2} className='virus2' alt="" />
            </footer>


            <section id='copy-rights' className='overflow-hidden'>
                <div className='container'>
                    <div className='d-flex flex-md-row flex-column justify-content-between align-items-center'>
                        <p className='mb-md-0 mb-2'>Covid-19 © 2020 All Right Reserved</p>
                        <div>
                            <Link className='me-4 pe-4 terms' to=''>Terms of Service</Link>
                            <Link to=''>Privacy Policy</Link>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

