import React, { useState } from 'react';
import virus1 from '../images/counter/icon-1.png';
import virus2 from '../images/counter/icon-2.png';
import virus3 from '../images/counter/icon-3.png';
// counter
import CountUp from 'react-countup';

export default function Counter() {

    const [counter, setCounter] = useState(false);
    window.addEventListener('scroll', () => {
        let count = document.getElementById('counter');
        let countPos = count.getBoundingClientRect().top;
        if (countPos < window.innerHeight) {
            setCounter(true);
        } else {
            setCounter(false);
        }
    })

    return (
        <>
            <section id='counter' className='py-5 overflow-hidden'>
                <div className='container'>
                    <div className='row g-0'>
                        <div className='col-md-4'>
                            <div className='item'>
                                <img src={virus1} alt="" />
                                <span><CountUp duration={3} end={counter ? 1531188 : 0} /></span>
                                <p className='mb-md-0 mb-5'>Active Cases</p>
                            </div>
                        </div>
                        <div className='col-md-4'>
                            <div className='item'>
                                <img src={virus2} alt="" />
                                <span><CountUp duration={3} end={counter ? 337276 : 0} /></span>
                                <p className='mb-md-0 mb-5'>Recovered Cases</p>
                            </div>
                        </div>
                        <div className='col-md-4'>
                            <div className='item'>
                                <img src={virus3} alt="" />
                                <span><CountUp duration={3} end={counter ? 89575 : 0} /></span>
                                <p className=''>Total Deaths</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}
