import React from 'react';
import { Link } from 'react-router-dom';
import protect1 from '../images/protect/protect-1.png';
import protect2 from '../images/protect/protect-2.png';
import protect3 from '../images/protect/protect-3.png';
import virus1 from '../images/protect/anim-icon-1.png';
import virus2 from '../images/protect/anim-icon-3.png';
import Fade from 'react-reveal/Fade';

export default function Protect() {
    return (
        <>
            <section id='protect' className='position-relative overflow-hidden mySec'>
                <div className='container'>

                    <Fade top>
                        <div className='protect-title'>
                            <h6>How To Protect</h6>
                            <h2>Take Steps To Protect <br />
                                Yourself</h2>
                        </div>
                    </Fade>

                    <div className='row g-4 py-5'>

                        <Fade bottom>
                            <div className='col-md-4'>
                                <div className='item'>
                                    <img src={protect1} className='w-50 rounded-circle' alt="" />
                                    <h3>Wear A Face Mask</h3>
                                    <p>Coronavirus disease (COVID-19) is an infectious disease caused by a newly discovered coronavirus.</p>
                                </div>
                            </div>
                        </Fade>

                        <Fade bottom>
                            <div className='col-md-4'>
                                <div className='item'>
                                    <img src={protect2} className='w-50 rounded-circle' alt="" />
                                    <h3>Wash Your Hands</h3>
                                    <p>Coronavirus disease (COVID-19) is an infectious disease caused by a newly discovered coronavirus.</p>
                                </div>
                            </div>
                        </Fade>

                        <Fade bottom>
                            <div className='col-md-4'>
                                <div className='item'>
                                    <img src={protect3} className='w-50 rounded-circle' alt="" />
                                    <h3>Avoid Close Contact</h3>
                                    <p>Coronavirus disease (COVID-19) is an infectious disease caused by a newly discovered coronavirus.</p>
                                </div>
                            </div>
                        </Fade>

                    </div>

                    <Fade bottom>
                        <div className='btn-outer'><Link to=''>How to Clean Hands?</Link></div>
                    </Fade>

                    <img src={virus1} className='virus1' alt="" />
                    <img src={virus2} className='virus2' alt="" />
                </div>
            </section>
        </>
    )
}
