import React from 'react';
import map from '../images/died-map/map.png';
import Fade from 'react-reveal/Fade';

export default function DiedMap() {
    return (
        <>
            <section id='died-map' className='poistion-relative overflow-hidden'>
                <div className='container'>

                    <Fade top>
                        <div className='title'>
                            <h6>Died For Covid-19</h6>
                            <h2>More Than 100k Died in <br />
                                Covid-19</h2>
                        </div>
                    </Fade>
                    
                    <Fade bottom>
                        <img src={map} className='w-100' alt="" />
                    </Fade>

                </div>
            </section>
        </>
    )
}
